
.welcome-container {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(135deg, #1e1e2e 0%, #2d2d44 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  overflow-y: auto;
}

.welcome-content {
  max-width: 900px;
  width: 100%;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 40px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.welcome-title {
  text-align: center;
  font-size: 32px;
  color: #fff;
  margin: 0 0 30px 0;
  text-shadow: 0 0 20px rgba(167, 139, 250, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;

  i {
    color: #a78bfa;
    animation: starPulse 2s ease-in-out infinite;
  }
}

@keyframes starPulse {
  0%,
  100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.7;
  }
}

.tabs-nav {
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  flex-wrap: wrap;
  justify-content: center;
}

.tab-btn {
  flex: 1;
  min-width: 150px;
  padding: 12px 20px;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.7);
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;

  i {
    font-size: 18px;
  }

  &:hover {
    background: rgba(255, 255, 255, 0.15);
    border-color: rgba(167, 139, 250, 0.5);
    color: #fff;
  }

  &.active {
    background: linear-gradient(135deg, #a78bfa 0%, #ec4899 100%);
    border-color: #a78bfa;
    color: #fff;
    box-shadow: 0 4px 15px rgba(167, 139, 250, 0.4);
  }
}

.tab-content {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 15px;
  padding: 30px;
  min-height: 400px;
  margin-bottom: 30px;
  color: #fff;
}

.content-section {
  h2 {
    color: #a78bfa;
    font-size: 24px;
    margin: 0 0 20px 0;
    display: flex;
    align-items: center;
    gap: 10px;
  }

  p {
    line-height: 1.8;
    font-size: 16px;
    color: rgba(255, 255, 255, 0.9);
    margin-bottom: 15px;
  }
}

.unit-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.unit-item {
  background: rgba(255, 255, 255, 0.05);
  padding: 15px 20px;
  border-radius: 10px;
  border-left: 4px solid #a78bfa;
  display: flex;
  align-items: center;
  gap: 15px;

  i {
    font-size: 24px;
    color: #a78bfa;
  }

  strong {
    color: #ec4899;
    font-size: 18px;
  }
}

.gameplay-item,
.note-item {
  background: rgba(255, 255, 255, 0.05);
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 15px;
  display: flex;
  align-items: flex-start;
  gap: 15px;

  i {
    font-size: 28px;
    color: #a78bfa;
    flex-shrink: 0;
  }

  strong {
    font-size: 18px;
    color: #ec4899;
    display: block;
    margin-bottom: 8px;
  }

  p {
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
    font-size: 14px;
  }
}

.note-item {
  &.warning {
    border-left: 4px solid #f59e0b;
    i {
      color: #f59e0b;
    }
  }

  &.info {
    border-left: 4px solid #3b82f6;
    i {
      color: #3b82f6;
    }
  }

  &.tip {
    border-left: 4px solid #10b981;
    i {
      color: #10b981;
    }
  }
}

.enter-game-btn {
  width: 100%;
  padding: 20px;
  background: linear-gradient(135deg, #a78bfa 0%, #ec4899 100%);
  border: none;
  border-radius: 15px;
  color: white;
  font-size: 24px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
  box-shadow: 0 6px 20px rgba(167, 139, 250, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;

  i {
    font-size: 28px;
  }

  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 10px 30px rgba(167, 139, 250, 0.6);
  }

  &:active {
    transform: translateY(-1px);
  }
}

/* ===== 偶像介绍样式 ===== */
.idols-section {
  .idols-intro {
    text-align: center;
    margin-bottom: 20px;
    font-size: 15px;
  }
}

.unit-filter {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
  flex-wrap: wrap;
  justify-content: center;
}

.unit-filter-btn {
  padding: 8px 16px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: rgba(255, 255, 255, 0.8);
  border-radius: 20px;
  cursor: pointer;
  font-size: 13px;
  transition: all 0.3s;

  &:hover {
    background: rgba(255, 255, 255, 0.2);
    color: #fff;
  }

  &.active {
    background: linear-gradient(135deg, #a78bfa 0%, #ec4899 100%);
    border-color: #a78bfa;
    color: #fff;
    box-shadow: 0 2px 10px rgba(167, 139, 250, 0.4);
  }
}

.idols-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: 15px;
  max-height: 400px;
  overflow-y: auto;
  padding: 10px;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(167, 139, 250, 0.5);
    border-radius: 10px;

    &:hover {
      background: rgba(167, 139, 250, 0.7);
    }
  }
}

.idol-card {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s;
  border: 2px solid rgba(255, 255, 255, 0.1);

  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 20px rgba(167, 139, 250, 0.4);
    border-color: rgba(167, 139, 250, 0.6);
  }
}

.idol-image-wrapper {
  width: 100%;
  aspect-ratio: 1 / 1;
  overflow: hidden;
  background: linear-gradient(135deg, rgba(167, 139, 250, 0.2), rgba(236, 72, 153, 0.2));
  display: flex;
  align-items: center;
  justify-content: center;
}

.idol-thumb {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.idol-info-brief {
  padding: 10px;
  text-align: center;

  h4 {
    margin: 0 0 5px 0;
    font-size: 14px;
    color: #fff;
  }

  .idol-unit-tag {
    margin: 0;
    font-size: 11px;
    color: rgba(255, 255, 255, 0.6);
  }
}

/* 偶像详情弹窗 */
.idol-detail-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  animation: fadeIn 0.3s ease;
  padding: 20px;
}

.idol-detail-content {
  background: linear-gradient(135deg, #1e1e2e 0%, #2d2d44 100%);
  border-radius: 20px;
  padding: 30px;
  max-width: 800px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  border: 2px solid rgba(167, 139, 250, 0.3);
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    transform: translateY(50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.detail-close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  background: rgba(255, 255, 255, 0.1);
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(255, 255, 255, 0.7);
  font-size: 20px;
  transition: all 0.3s;

  &:hover {
    background: rgba(255, 0, 0, 0.3);
    color: #fff;
  }
}

.detail-layout {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 30px;
  margin-top: 20px;

  @media (max-width: 600px) {
    grid-template-columns: 1fr;
  }
}

.detail-idol-image {
  width: 100%;
  border-radius: 15px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.detail-right {
  h3 {
    font-size: 28px;
    color: #fff;
    margin: 0 0 15px 0;
  }
}

.detail-unit {
  display: inline-block;
  padding: 6px 16px;
  border-radius: 20px;
  border-left: 4px solid;
  background: rgba(255, 255, 255, 0.1);
  font-size: 14px;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 20px;
}

.detail-info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 15px;
  margin-bottom: 20px;
}

.info-item {
  background: rgba(255, 255, 255, 0.05);
  padding: 12px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  border-left: 3px solid #a78bfa;

  i {
    color: #a78bfa;
    font-size: 18px;
  }

  span {
    color: rgba(255, 255, 255, 0.9);
    font-size: 14px;
  }
}

.detail-description {
  h4 {
    color: #a78bfa;
    font-size: 18px;
    margin: 0 0 10px 0;
  }

  p {
    color: rgba(255, 255, 255, 0.8);
    font-size: 15px;
    line-height: 1.8;
    margin: 0;
  }
}
